<template>
  <div>
    <h1>{{ id ? "" : "" }}</h1>
    <el-form
      label-width="120px"
      :model="model"
      :rules="categoryFormrules"
      ref="categoryForm"
    >
      <el-form-item label="">
        <el-select
          v-model="model.parent"
          placeholder=""
          style="width: 30%"
          clearable
        >
          <el-option
            v-for="item in parents"
            :key="item._id"
            :label="item.name"
            :value="item._id"
          ></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="" prop="name">
        <el-input v-model="model.name" style="width: 30%"></el-input>
      </el-form-item>
      <el-form-item label="" prop="category">
        <el-select
          v-model="model.category"
          placeholder=""
          style="width: 30%"
        >
          <el-option
            v-for="(item, index) in ['', '', '']"
            :key="index"
            :label="item"
            :value="item"
          ></el-option>
        </el-select>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="save"></el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
export default {
  name: "CategoryEdit",
  props: ["id"],
  data() {
    return {
      model: {
        name: "",
        category: "",
      },
      parents: [],
      // 
      categoryFormrules: {
        name: [{ required: true, message: "", trigger: "blur" }],
        category: [
          { required: true, message: "", trigger: "change" },
        ],
      },
    };
  },
  methods: {
    // 
    save() {
      this.$refs.categoryForm.validate(async (valid) => {
        if (!valid) {
          this.$message.error("");
        } else {
          let res;
          if (this.id) {
            res = await this.$http.put(
              `rest/categories/${this.id}`,
              this.model
            );
          } else {
            res = await this.$http.post("rest/categories", this.model);
          }
          this.$router.push("/categories/list");
          this.$message({
            type: "success",
            message: "",
          });
        }
      });
    },
    // idname
    async getName() {
      // console.log(this.id);
      const res = await this.$http.get(`rest/categories/${this.id}`);
      this.model = { ...this.model, ...res.data };
    },
    async getParentData() {
      const res = await this.$http.get("rest/categories");
      // console.log(res);
      const data = res.data.filter((item) => {
        if (item.category === "" || item.category === "") return true;
      });
      // console.log(data);
      this.parents = { ...this.parents, ...data };
      // console.log(this.parents);
    },
  },
  created() {
    // ididid
    this.id && this.getName();
    this.getParentData();
  },
};
</script>
