<template>
  <div>
    <h1>{{ id ? "" : "" }}</h1>
    <el-form
      label-width="120px"
      :model="model"
      :rules="adminFormrules"
      ref="adminForm"
    >
      <el-form-item label="" prop="username">
        <el-input
          v-model="model.username"
          style="width: 30rem"
          :disabled="id ? true : false"
        ></el-input>
      </el-form-item>
      <el-form-item :label="id ? '' : ''" prop="password">
        <el-input
          v-model="model.password"
          show-password
          style="width: 30rem"
        ></el-input>
      </el-form-item>
      <el-form-item label="" prop="password" v-if="id">
        <el-input
          v-model="password.newPassword"
          show-password
          style="width: 30rem"
        ></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="save"></el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
export default {
  name: "AdminEdit",
  props: ["id"],
  data() {
    return {
      model: {
        username: "",
        password: "",
      },
      password: {
        oldPassword: "",
        newPassword: "",
      },
      // 
      adminFormrules: {
        username: [
          { required: true, message: "", trigger: "blur" },
        ],
        password: [{ required: true, message: "", trigger: "blur" }],
      },
    };
  },
  methods: {
    // 
    save() {
      this.$refs.adminForm.validate(async (valid) => {
        // 
        if (!valid) {
          this.$message.error("");
        } else {
          let res;
          // id
          if (this.id) {
            const userInfo = { ...this.model, ...this.password };
            // console.log(userInfo);
            // 
            if (userInfo.password === userInfo.newPassword) {
              this.$message({
                type: "error",
                message: "",
              });
            } else {
              res = await this.$http.put(`userInfo/${this.id}`, userInfo);
              if (res.data.success === false) {
                return this.$message({
                  type: "error",
                  message: res.data.message,
                });
              }
              this.$message({
                type: "success",
                message: "",
              });
              this.$router.push("/admins/list");
            }
          } else {
            res = await this.$http.post("reguser", this.model);
            // console.log(res);
            // 
            if (res.data.keyPattern) {
              this.$message({
                type: "error",
                message: "",
              });
            } else {
              this.$message({
                type: "success",
                message: "",
              });
              this.$router.push("/admins/list");
            }
          }
        }
      });
    },
    // 3.id
    async getUserInfoById() {
      const res = await this.$http.get(`userInfo/${this.id}`);
      // console.log(res);
      this.model.username = res.data.username;
      this.password.oldPassword = res.data.password;
    },
  },
  created() {
    this.id && this.getUserInfoById();
  },
};
</script>

